/* Estilos adicionais para as tabelas */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1rem;
}

th, td {
  border: 1px solid #e5e7eb;
  padding: 0.75rem;
  text-align: left;
}

th {
  background-color: #f3f4f6;
  font-weight: bold;
}

.table-container {
  overflow-x: auto;
}

/* Estilos para melhorar a responsividade */
@media (max-width: 640px) {
  table {
    font-size: 0.875rem;
  }
  
  th, td {
    padding: 0.5rem;
  }
}